@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bigelow+Rules&display=swap);
body {
  margin: 0;
  font-family: 'Lobster', cursive;
  background: linear-gradient(to right, #44a08d, #093637); 
}

h1{
  text-align: center;
  font-family: 'Bigelow Rules', cursive;
  font-size: 3.5rem;
  letter-spacing: 0.3em;
  text-transform: uppercase;
  color: rgb(197, 33, 66);
}


.cardContainer{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;background-color:#95dada;border:1px solid gray;border-radius:5px;padding:25px;max-width:200px;height:auto;cursor:pointer;-moz-osx-font-smoothing:grayscale;-webkit-backface-visibility:hidden;backface-visibility:hidden;-webkit-transform:translateZ(0);transform:translateZ(0);transition:-webkit-transform .25s ease-out;transition:transform .25s ease-out;transition:transform .25s ease-out, -webkit-transform .25s ease-out}.cardContainer h2{font-family:"Bigelow Rules",cursive;font-size:1.8em}.cardContainer:hover{-webkit-transform:scale(1.05);transform:scale(1.05)}
.cardList{max-width:1000px;margin:0 auto;display:grid;grid-template-columns:repeat(4, 1fr);grid-gap:20px}@media screen and (max-width: 1300px){.cardList{grid-template-columns:repeat(3, 1fr)}}@media screen and (max-width: 840px){.cardList{grid-template-columns:repeat(2, 1fr)}}@media screen and (max-width: 540px){.cardList{grid-template-columns:repeat(1, 1fr)}}
.searchField{-webkit-appearance:none;border:none;outline:none;padding:10px;width:300px;line-height:30px;margin-bottom:30px;justify-self:center;background-color:transparent;color:#fff;font-family:"Bigelow Rules",cursive;font-size:2em;letter-spacing:.2em;margin:0 auto;text-align:center}.searchField::-webkit-input-placeholder{color:#fff}.searchField:-ms-input-placeholder{color:#fff}.searchField::placeholder{color:#fff}
.App{display:grid;text-align:center}
